import React from 'react';
import clsx from 'clsx';
import { SnackbarContent, IconButton, makeStyles } from '@material-ui/core';
import { amber, green } from '@material-ui/core/colors';
import CloseIcon from '@material-ui/icons/Close';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import WarningIcon from '@material-ui/icons/Warning';
import ErrorIcon from '@material-ui/icons/Error';
import InfoIcon from '@material-ui/icons/Info';
import { SvgIconProps } from '@material-ui/core/SvgIcon';

interface CustomSnackbarContentProps {
  className?: string;
  message: string;
  onClose: (
    e: React.SyntheticEvent<any, Event>,
    reason?: string | undefined
  ) => void;
  variant: 'success' | 'warning' | 'error' | 'info';
}

const variantIcon = {
  success: CheckCircleIcon,
  warning: WarningIcon,
  error: ErrorIcon,
  info: InfoIcon
};

const useStyles = makeStyles(theme => ({
  success: {
    backgroundColor: green[600]
  },
  error: {
    backgroundColor: theme.palette.error.dark
  },
  info: {
    backgroundColor: theme.palette.primary.main
  },
  warning: {
    backgroundColor: amber[700]
  },
  icon: {
    fontSize: 20
  },
  iconVariant: {
    opacity: 0.9,
    marginRight: theme.spacing(1)
  },
  message: {
    display: 'flex',
    alignItems: 'center'
  }
}));

const CustomSnackbarContent = (props: CustomSnackbarContentProps) => {
  const classes = useStyles();
  const { className, message, onClose, variant, ...other } = props;
  const Icon: React.ComponentType<SvgIconProps> = variantIcon[variant];

  return (
    <SnackbarContent
      className={clsx(classes[variant], className)}
      aria-describedby="client-snackbar"
      message={[
        <span
          id="client-snackbar"
          className={classes.message}
          key={`message-${classes.message}`}
        >
          <Icon className={clsx(classes.icon, classes.iconVariant)} />
          {message}
        </span>
      ]}
      action={[
        <IconButton
          key="close"
          aria-label="close"
          color="inherit"
          onClick={onClose}
        >
          <CloseIcon className={classes.icon} />
        </IconButton>
      ]}
      {...other}
    />
  );
};

export default CustomSnackbarContent;
